import { useHost } from 'atomico';

interface SlotElements {
  [slot: string]: Element[] | Element;
}

interface Slots extends SlotElements {
  children: Element[];
}

function useSlots(): Slots {
  const host = useHost();
  /* istanbul ignore else*/
  if (!host.slots) {
    const children: any[] = [];
    const slots: Slots = { children };
    const { current } = host;
    // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
    // @ts-ignore
    Array.from(current!.children).forEach((childNode: HTMLElement) => {
      const slot = childNode.getAttribute('slot');
      /* istanbul ignore else*/
      if (slot) slots[slot] = childNode;
      children.push(childNode);
    });
    host.slots = slots;
  }
  return host.slots;
}

export { useSlots, Slots };
